<template>
    <div id="problem">
        <h1 id="title">
            {{ problem.title }}
        </h1>
        <article id="description">
            {{ problem.description }}
        </article>

        <h5> </h5>
        <table class="table">
            <thead>
                <th>   ()</th>
                <th>   ()</th>
                <th>  (KB)</th>
            </thead>
            <tbody>
                <tr>
                    <td>{{ problem.cpuTimeLimit }}</td>
                    <td>{{ problem.wallTimeLimit }}</td>
                    <td>{{ problem.memoryLimit }}</td>
                </tr>
            </tbody>
        </table>
        <h5> </h5>
        <table class="table">
            <thead>
                <th></th>
                <th></th>
            </thead>
            <tbody>
                <tr>
                    <td>
                        <pre>{{ problem.testcases.input }}</pre>
                    </td>
                    <td>
                        <pre>{{ problem.testcases.output }}</pre>
                    </td>
                </tr>
            </tbody>
        </table>
        <ul class="nav nav-tabs">
            <li class="nav-item">
                <span class="nav-link" href="#" v-on:click="activeTab1()"></span>
            </li>
            <li class="nav-item">
                <span class="nav-link" v-on:click="activeTab2()"></span>    
            </li>
            <li class="nav-item">
                <span class="nav-link" v-on:click="submit()"></span>
            </li>
            <!-- TODO:    -->
            <div class="dropdown">
                <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-expanded="false">
                    {{ selectedLanguageName }}
                </button>
                <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <li v-for="(language, id, index) in languages" v-bind:key="index" @click="onLanguageChanged($event)" v-bind:id="id" class="dropdown-item">{{ language.name }}</li>
                </ul>
            </div>
        </ul>
        <codemirror v-show="activeTab" v-model="code" :options="CodemirrorOptions"></codemirror>
        <!-- List of judge result -->
        <ul v-show="!activeTab" class="list-group list-group-flush">
            <li class="result-list list-group-item" v-for="(result, index) in submitResults" v-bind:key="index">
                <div>
                </div>
                <span ref="judging" class="result-span">
                    {{ result }}
                </span>
            </li>
        </ul>
        <!-- Modal area -->
        <div class="modal fade" ref="judgeResult" tabindex="-1">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title"> </h5>
                    </div>
                    <div class="modal-body">
                        <h1 v-if="judgeStatus.judgeFailed"></h1>
                        <h1 v-else></h1>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { languages } from '../../lib/languages';
import { judgeResultMap } from '../../lib/judgeResultStatusCode';
import axios from 'axios';
import io from 'socket.io-client';
import $ from 'jquery';
import 'codemirror/mode/javascript/javascript';

/**
 * CodeMirror Syntax Highlighting
 * https://codemirror.net/demo/loadmode.html
 */

export default {
    props: {
        id: null,
    },
    data() {
        return {
            socket: io.connect('ws://localhost:3000/problem'),
            code: '',
            options: {
                selectOnLineNumbers: false,
                automaticLayout: true,
                wordWrap: 'on'
            },
            CodemirrorOptions: {
                tabSize: 4,
                styleActiveLine: true,
                lineNumbers: true,
                line: true,
                mode: 'javascript',
                lineWrapping: true,
                theme: 'ayu-mirage',
                matchBrackets: true,
                autoCloseBrackets: true,
                autoCloseTags: true,
                foldGutter: true,
                gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
            },
            activeTab: true,
            problem: {
                title: "",
                description: "",
                author: null,
                cpuTimeLimit: null,
                wallTimeLimit: null,
                memoryLimit: null,
                testcases: {},
                testcasesCount: ""
            },
            languages: languages,
            selectedLanguage: Object.keys(languages)[0],
            selectedLanguageName: Object.values(languages)[0].name,
            submitResults: [],
            judgeStatus: {
                judgeCompleted: false,
                judgeFailed: false,
            }
        }
    },
    created() {
        axios.get(`/problems/${this.id}`).then(problem_response => {
            this.problem.title = problem_response.data.title;
            this.problem.description = problem_response.data.description;
            this.problem.author = problem_response.data.author;
            this.problem.cpuTimeLimit = problem_response.data.cpu_time_limit;
            this.problem.wallTimeLimit = problem_response.data.wall_time_limit;
            this.problem.memoryLimit = problem_response.data.memory_limit;
            this.problem.testcases = problem_response.data.example_testcase;
            this.submitResults = new Array(problem_response.data.testcases_count);
            this.submitResults.fill(" ");
        }).catch(error => console.error(error));
        
        this.socket.on("submit_result", (data) => {
            // TODO():  
            this.judgeStatus.judgeFailed = false;
            this.completedJudgementOfOneTestCase(data);
            console.log(data);
        });

        this.socket.on("judge_result", (data) => {
            // TODO:  
            console.log(data);
            $(this.$refs.judgeResult).modal("show");
        });
    },
    methods: {
        onMounted (editor) {
            this.editor = editor;
        },
        onLanguageChanged ($event) {
            this.selectedLanguage = parseInt($event.target.id);
            this.selectedLanguageName = languages[this.selectedLanguage].name;
        },
        activeTab1 () {
            this.activeTab = true;
        },
        activeTab2 () {
            this.activeTab = false;
        },
        judging () {
            //  .
            document.getElementsByClassName("result-list").forEach(item => {
                item.children[1].style.paddingLeft = "15px";
                item.children[1].innerHTML = " ";
                item.children[1].style.color = "black";

                item.children[0].classList = "spinner-border spinner-border-sm";
            });
        },
        completedJudgementOfOneTestCase (judge_result) {
            //      .
            const result_li = document.getElementsByClassName("result-list");
            if (judge_result.description === 'Accepted') {
                result_li[judge_result.testcase_id].children[1].style.color = "green";
            } else {
                result_li[judge_result.testcase_id].children[1].style.color = "red";
                this.judgeStatus.judgeFailed = true;
            }
            result_li[judge_result.testcase_id].children[1].innerHTML = `${judgeResultMap[judge_result.description]} (${judge_result.time}, ${judge_result.memory / 1000}MB)`;
            // spinner .
            result_li[judge_result.testcase_id].children[1].style.paddingLeft = "";
            result_li[judge_result.testcase_id].children[0].classList = "";
        },
        submit () {
            // (required)  judge0 422 Unprocessable.
            if (this.code.trim() !== '') {
                const context = {
                    "problem_id": this.id,
                    "code": this.code,
                    "language": this.selectedLanguage
                };

                this.socket.emit("submit", context);
                // tab change
                this.activeTab2();
                this.judging();
            }
        }
    },
    destroyed() {
        // socket connection destroy when route changed.
        this.socket.close();
    }
}
</script>

<style scoped>
#problem {
    padding: 50px;
}

#description {
    margin-top: 15px;
    margin-bottom: 15px;
}

#editor {
    height: 600px;
}
</style>